import React from "react";
import {BrowserRouter, Switch, Route} from "react-router-dom";
import Page_1 from "./components/page_1/Page_1";
import Page_2 from "./components/page_2/Page_2";
import Page_3 from "./components/page_3/Page_3";
import Page_4 from "./components/page_4/Page_4";



function App() {
  return (
      <BrowserRouter>

          <Switch>
              <Route path="/" exact component={Page_1}/>
              <Route path="/page_2" exact component={Page_2}/>
              <Route path="/page_3" exact component={Page_3}/>
              <Route path="/page_4" exact component={Page_4}/>

          </Switch>
      </BrowserRouter>
  );
}

export default App;
